@import "styles/mixins";

.panel {
  width: 100%;
  min-height: 17vh;
  padding-top: 30px;
  padding-right: 130px;


  @include flex-flex-end();

  background-color: #162C4E;

  @media (max-width: 1100px) {
    padding-right: 0;

    justify-content: center;
  }

  @media (max-width: 768px) {
    padding-top: 30px;

    min-height: 160px;
  }

  @media (max-width: 360px) {
    min-height: 140px;
  }

  form {
    margin-top: -13px;
  }
}

.wrapper {
  width: 58%;

  @include flex-space-between();

  @media (max-width: 1100px) {
    flex-direction: column;
    row-gap: 27px;
  }

  @media (max-width: 360px) {
    flex-direction: column;
    row-gap: 10px;
  }
}